<template>
  <div>
    <div class="bg-white tw-rounded mb-2 py-2">
      <div class="text-2xl font-bold">벽</div>
      <div class="pt-2">벽은 타일을 조합해서 만듭니다.</div>
    </div>

    <div v-if="loading">LOADING</div>
    <div v-else>
      <div class="mb-2 grid gap-2 md:grid-cols-3 grid-cols-2">
        <div
          class="tw-rounded bg-white overflow-hidden"
          v-for="data in $store.state.listWall"
          :key="data.rid"
        >
          <button class="" @click="onEdit(data.rid)">
            <img
              class="h-64 object-cover"
              :src="$store.makePicUrl(data.wall_url)"
            />
          </button>
          <div class="px-2 py-1">
            <div class="flex text-left break-all">
              {{ data.wall_name }}
            </div>
            <div class="flex">
              <div class="ml-2 tw-ago">
                {{ $store.getAgo(data.last_edit) }}
              </div>
            </div>
            <div class="flex justify-end">
              <button @click="onDelete(data.rid)">
                <img class="w-6 h-6" src="/images/hero-trash.svg" />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="p-2 tw-rounded bg-white">
      <div class="flex justify-center items-center">
        <div class="p-1">새 벽 이름</div>
        <input
          v-model="inputAddName"
          class="ml-4 p-1 rounded-lg border-2 border-black"
        />
        <button
          class="ml-4 px-4 py-1 rounded-lg bg-indigo-600 text-white"
          @click="onAdd()"
        >
          추가
        </button>
      </div>
      <div class="text-red-600">{{ errorAddName }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      inputAddName: "",
      errorAddName: "",
      loadingDelete: false,
    };
  },
  async mounted() {
    if (this.$store.state.listWall === null) {
      // 리스트가 없으면 불러온다.
      const reqData = {
        api: "getWallList",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
      };
      this.loading = true;
      const res = await this.$store.callAsync(reqData);
      this.loading = false;
      if (res.code != 100) {
        console.log("getWallList fail", res);
        this.errorMessage = res.message;
        return;
      }
      console.log("getWallList ok", reqData, res);
      this.$store.commit("setListWall", res.listWall);
    }
  },
  methods: {
    async onAdd() {
      if (this.loadingAddName) {
        return;
      }

      const reqData = {
        api: "addWall",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        inputName: this.inputAddName,
      };

      this.errorAddName = "";
      this.loadingAddName = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingAddName = false;

      console.log("addWall res", res);
      if (res.code != 100) {
        this.errorAddName = res.message;
        return;
      }

      this.$store.commit("addWall", res.newWall);
      this.errorAddName = "";
    },
    onEdit(rid) {
      console.log("onEdit", rid);
      this.$router.push(`/wall/edit/${rid}`);
    },
    async onDelete(rid) {
      if (this.loadingDelete) {
        return;
      }

      console.log("onDelete", rid);

      const reqData = {
        api: "delWall",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        wallRid: rid,
      };

      this.loadingDelete = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingDelete = false;

      if (res.code != 100) {
        console.log("delWall fail", res);
        this.errorMessage = res.message;
        return;
      }

      this.$store.commit("delWall", res.delWallRid);
    },
  },
};
</script>
